@keyframes slideUp {
	from {
		transform: translateY(100px);
		opacity: 0;
	}
	to {
		transform: translateY(0);
		opacity: 1;
	}
}

@keyframes drawEllipse {
	0% {
		opacity: 0;
		stroke-dashoffset: 2800;
	}
	50% {
		opacity: 0.5;
		stroke-dashoffset: 1400;
	}
	100% {
		opacity: 1;
		stroke-dashoffset: 0;
	}
}

.hero {
	background-color: $color-jaguar;
	color: $color-default-white;
	margin-top: 92px;
	padding: 80px 0 0;

	@include vp-1439 {
		padding: 72px 0 0;
	}
	@include vp-1279 {
		padding: 64px 0 0;
	}
	@include vp-1023 {
		padding: 56px 0 0;
	}
	@include vp-767 {
		padding: 48px 0 0;
		margin-top: 78px;
	}
	@include vp-374 {
		padding: 40px 0 0;
		margin-top: 71px;
	}

	&__box {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 40px;
		align-items: center;
		justify-content: center;

		@include vp-1023 {
			grid-template-columns: 1fr;
			gap: 28px;
		}
	}

	&__left {
		grid-column: 1;
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		gap: 60px;
		z-index: 300;
		opacity: 0;
		transform: translateY(30px);
		animation: none; // Отключаем анимацию до загрузки лоадера
		will-change: transform;
		align-items: flex-start;
		text-align: left;

		@include vp-1439 {
			gap: 48px;
		}
		@include vp-1279 {
			gap: 40px;
		}
		@include vp-1023 {
			gap: 32px;
			align-items: center;
			text-align: center;
		}
		@include vp-767 {
			gap: 28px;
		}
		@include vp-374 {
			gap: 24px;
		}
	}

	&__name {
		font-family: $ff-rubik;
		font-size: 128px;
		font-weight: 600;
		line-height: 130px;
		text-transform: uppercase;
		padding: 0;
		margin: 0;
		max-width: 780px;
		z-index: 300;

		@include vp-1919 {
			font-size: 110px;
			line-height: 116px;
			max-width: 700px;
		}
		@include vp-1439 {
			font-size: 96px;
			line-height: 104px;
			max-width: 620px;
		}
		@include vp-1279 {
			font-size: 80px;
			line-height: 90px;
			max-width: 520px;
		}
		@include vp-1023 {
			font-size: 64px;
			line-height: 74px;
			max-width: 420px;
			margin-left: auto;
			margin-right: auto;
		}
		@include vp-767 {
			font-size: 48px;
			line-height: 58px;
			max-width: 320px;
		}
		@include vp-374 {
			font-size: 36px;
			line-height: 44px;
			max-width: 260px;
		}
	}

	&__desc {
		font-size: 32px;
		line-height: 34px;
		margin: 0;
		padding: 0;
		z-index: 300;

		@include vp-1919 {
			font-size: 28px;
			line-height: 32px;
		}
		@include vp-1439 {
			font-size: 24px;
			line-height: 28px;
		}
		@include vp-1279 {
			font-size: 20px;
			line-height: 24px;
		}
		@include vp-1023 {
			font-size: 18px;
			line-height: 22px;
		}
		@include vp-767 {
			font-size: 16px;
			line-height: 20px;
		}
		@include vp-374 {
			font-size: 14px;
			line-height: 18px;
		}
	}

	// Кнопка внутри hero
	&__wrapper .btn {
		align-self: flex-start;
		opacity: 0;
		transform: translateY(20px);
		transition: opacity 0.6s ease, transform 0.6s ease;

		@include vp-1023 {
			align-self: center;
		}
	}
	
	// Активируем анимацию кнопки после загрузки
	body.loaded &__wrapper .btn {
		opacity: 1;
		transform: translateY(0);
		animation-delay: 0.8s; // После текста
	}

	&__right {
		grid-column: 2;
		justify-self: end;

		@include vp-1023 {
			grid-column: 1;
			justify-self: center;
		}
	}

	&__inner {
		position: relative;
		height: auto;
		z-index: 100;
		pointer-events: none;
		user-select: none;
		max-width: 704px;
		margin: 0;

		@include vp-1439 {
			max-width: 620px;
		}
		@include vp-1279 {
			max-width: 520px;
		}
		@include vp-1023 {
			max-width: 420px;
			margin: 0 auto;
		}
		@include vp-767 {
			max-width: 340px;
		}
		@include vp-374 {
			max-width: 280px;
		}
	}

	&__ellipse {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 100%;
		height: auto;
		transform: translate(-50%, -50%);
		z-index: 100;
		color: $color-paua;
		pointer-events: none;

		path {
			stroke-dasharray: 2800;
			stroke-dashoffset: 2800;
			opacity: 0;
			// Анимация будет запущена только после загрузки лоадера
			animation: none;
		}
	}
	
	// Активируем анимацию круга только после загрузки
	body.loaded &__ellipse path {
		animation: drawEllipse 1s linear forwards;
		animation-delay: 0.5s; // Небольшая задержка после появления hero
	}

	&__img {
		position: relative;
		z-index: 200;
		display: block;
		max-width: 100%;
		height: auto;
		object-fit: cover;
		margin: 0 auto 0;
		opacity: 0;
		transform: scale(0.9);
		transition: opacity 0.8s ease, transform 0.8s ease;
	}
	
	// Активируем анимацию изображения после загрузки
	body.loaded &__img {
		opacity: 1;
		transform: scale(1);
		animation-delay: 1s; // После анимации круга
	}
	
	// Активируем анимацию текста после загрузки
	body.loaded &__wrapper {
		opacity: 1;
		transform: translateY(0);
		animation: slideUp 1s ease-out forwards;
		animation-delay: 0.2s; // Сразу после появления hero
	}
}
