.ticket-card {
	padding: 39px;
	border: 1px solid $color-jaguar-20;
	min-height: 460px;
	border-radius: 20px;
	width: 360px; // фиксированная ширина
	max-width: 360px; // ограничиваем максимальную ширину
	flex-shrink: 0; // не сжимаем карточки
	display: flex;
	flex-direction: column;
	align-items: center;
	cursor: pointer;
	transition: background-color $trans-600, color $trans-600, border-color $trans-600, box-shadow $trans-600;
	will-change: background-color, box-shadow;

	@include vp-1439 {
		width: 340px;
		max-width: 340px;
	}
	@include vp-1279 {
		width: 320px;
		max-width: 320px;
	}
	@include vp-1023 {
		width: 300px;
		max-width: 300px;
		min-height: 420px;
	}
	@include vp-767 {
		width: 280px;
		max-width: 280px;
		min-height: 400px;
		padding: 28px;
	}
	@include vp-374 {
		width: 260px;
		max-width: 260px;
		min-height: 380px;
		padding: 24px;
	}

	&--active {
		min-height: 500px;
		background-color: $color-jaguar;
		color: $color-default-white;
		border: 1px solid $color-jaguar;
		box-shadow: 0 12px 30px rgba($color-jaguar, 0.2);

		@include vp-1023 {
			min-height: 440px; // было 460
		}
		@include vp-767 {
			min-height: 420px; // было 440
		}
		@include vp-374 {
			min-height: 400px; // было 420
		}
	}

	&__title {
		font-size: 30px;
		line-height: 34px;
		font-weight: 600;
		margin: 0 0 auto;

		@include vp-1279 {
			font-size: 26px;
			line-height: 30px;
		}
		@include vp-1023 {
			font-size: 24px;
			line-height: 28px;
		}
		@include vp-767 {
			font-size: 22px;
			line-height: 26px;
		}
		@include vp-374 {
			font-size: 20px;
			line-height: 24px;
		}
	}

	&__inner {
		display: flex;
		flex-direction: column;
		gap: 20px;

		@include vp-1023 {
			gap: 16px;
		}
		@include vp-767 {
			gap: 14px;
		}
		@include vp-374 {
			gap: 12px;
		}
	}

	&__price {
		padding: 0;
		margin: 0;
		font-weight: 600;
		font-size: 50px;
		line-height: 54px;
		text-align: center;

		@include vp-1279 {
			font-size: 44px;
			line-height: 48px;
		}
		@include vp-1023 {
			font-size: 40px;
			line-height: 44px;
		}
		@include vp-767 {
			font-size: 36px;
			line-height: 40px;
		}
		@include vp-374 {
			font-size: 32px;
			line-height: 36px;
		}
	}

	&__desc {
		text-align: center;
		font-size: 18px;
		line-height: 22px;
		padding: 0;
		margin: 0;
		max-width: 250px;

		@include vp-1279 {
			font-size: 16px;
			line-height: 20px;
		}
		@include vp-767 {
			font-size: 14px;
			line-height: 18px;
			max-width: 220px;
		}
	}

	&__btn {
		margin: auto 0 0;

		@include vp-767 {
			width: 100%;
		}
	}
}