.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #F9F9F9;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  opacity: 1;
  visibility: visible;
  transition: opacity 0.5s ease, visibility 0.5s ease;
}
.loader:not(.hidden) body {
  overflow: hidden;
}
.loader__content {
  text-align: center;
  color: #141024;
}
.loader__spinner {
  position: relative;
  width: 60px;
  height: 60px;
  margin: 0 auto 30px;
  animation: fadeInUp 0.8s ease-out 0.2s both;
}
.loader__circle {
  width: 100%;
  height: 100%;
  border: 3px solid #EAE9E9;
  border-top: 3px solid #D52027;
  border-right: 3px solid #D52027;
  border-radius: 50%;
  animation: rotate 1s linear infinite;
  position: relative;
  transform-origin: center;
  will-change: transform;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader__circle:hover {
  animation-play-state: paused;
}
.loader__circle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 8px;
  background: #D52027;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: pulse 1.5s ease-in-out infinite;
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
  50% {
    opacity: 0.5;
    transform: translate(-50%, -50%) scale(1.2);
  }
}
.loader__text {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.loader__title {
  font-size: 32px;
  font-weight: 700;
  color: #141024;
  letter-spacing: 1px;
  animation: fadeInUp 0.8s ease-out 0.3s both;
}
@media (max-width: 1023px) {
  .loader__title {
    font-size: 28px;
  }
}
@media (max-width: 767px) {
  .loader__title {
    font-size: 24px;
  }
}
@media (max-width: 374px) {
  .loader__title {
    font-size: 20px;
  }
}
.loader__subtitle {
  font-size: 14px;
  font-weight: 400;
  color: #B5AFA6;
  opacity: 0.8;
  animation: fadeInUp 0.8s ease-out 0.5s both;
}
@media (max-width: 767px) {
  .loader__subtitle {
    font-size: 12px;
  }
}
.loader.hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

body.loading #main-content .header,
body.loading #main-content .footer {
  opacity: 0;
  transform: translateY(-20px);
  transition: none;
}
body.loading #main-content .hero.u-defer {
  opacity: 0;
  transform: translateY(30px);
  transition: none;
}
