.contacts__map {
  isolation: isolate;
  position: relative;
  z-index: 0;
  width: 100%;
  height: 400px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  background-color: #f5f5f5;
  transition: opacity 0.3s ease;
}
.contacts__map::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  border: 3px solid #e0e0e0;
  border-top: 3px solid #007bff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  z-index: 1;
}
.contacts__map.loaded::before {
  display: none;
}
.contacts__map iframe {
  width: 100%;
  height: 100%;
  border: none;
  display: block;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.contacts__map iframe.loaded {
  opacity: 1;
}
@media (max-width: 1023px) {
  .contacts__map {
    height: 350px;
    border-radius: 8px;
  }
}
