.footer {
	padding: 30px 0 15px;
	background-color: $color-jaguar;

	@include vp-1023 {
		padding: 24px 0 12px;
	}
	@include vp-767 {
		padding: 20px 0 10px;
	}

	&__wrapper {
		display: grid;
		grid-template-columns: auto 1fr auto;
		align-items: center;
		justify-content: normal;
		margin: 0 0 20px;

		@include vp-767 {
			grid-template-columns: 1fr;
			row-gap: 16px;
		}
	}

	&__logo {
		display: block;
		width: 84px;
		height: 60px;
		user-select: none;
		grid-column: 1;
		justify-self: start;

		@include vp-767 {
			display: none;
		}
	}

	&__logo-img {
		display: block;
		width: 84px;
		height: 60px;
		overflow: hidden;

		@include vp-767 {
			width: 54px;
			height: 39px;
		}
	}

	&__user-list {
		list-style-type: none;
		margin: 0;
		padding: 0;
		display: flex;
		align-items: center;
		gap: 20px;
		justify-content: center;
		grid-column: 2;
		justify-self: center;

		@include vp-767 {
			grid-column: 1;
			justify-self: center;
			gap: 12px;
		}
	}

	&__user-link {
		display: flex;
		justify-content: center;
		align-items: center;
		border: none;
		background-color: $color-fire-red;
		padding: 0;
		margin: 0;
		height: 56px;
		width: 56px;
		border-radius: 50%;
		cursor: pointer;
		transition: background-color $trans-default;

		@include vp-1439 {
			height: 52px;
			width: 52px;
		}
		@include vp-1279 {
			height: 48px;
			width: 48px;
		}
		@include vp-1023 {
			height: 44px;
			width: 44px;
		}
		@include vp-767 {
			height: 40px;
			width: 40px;
		}
		@include vp-374 {
			height: 36px;
			width: 36px;
		}

		&:hover,
		&:focus {
			background-color: $color-bright-red;
		}
	}

	&__user-icon {
		display: block;
		width: 24px;
		height: 24px;
		color: $color-default-white;

		@include vp-1439 {
			width: 22px;
			height: 22px;
		}
		@include vp-1279 {
			width: 20px;
			height: 20px;
		}
		@include vp-1023 {
			width: 18px;
			height: 18px;
		}
		@include vp-767 {
			width: 16px;
			height: 16px;
		}
		@include vp-374 {
			width: 14px;
			height: 14px;
		}
	}

	&__phone {
		display: block;
		color: $color-default-white;
		font-weight: 600;
		font-size: 30px;
		line-height: 34px;
		grid-column: 3;
		justify-self: end;
		white-space: nowrap;

		@include vp-1279 {
			font-size: 24px;
			line-height: 26px;
		}

		@include vp-1023 {
			font-size: 22px;
			line-height: 24px;
		}

		@include vp-767 {
			grid-column: 1;
			justify-self: center;
			text-align: center;
		}

		@include vp-374 {
			font-size: 16px;
			line-height: 20px;
		}
	}

	&__text {
		color: $color-default-white-07;
		font-size: 10px;
		line-height: 12px;
		margin: 0;
		padding: 0;
		text-align: right;

		@include vp-767 {
			text-align: center;
			line-height: 14px;
		}
	}
}
