.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	background-color: $color-fire-red;
	padding: 25px 40px;
	outline: none;
	cursor: pointer;
	color: $color-default-white;
	font-size: 18px;
	line-height: 22px;
	border-radius: 5px;
	max-width: 340px;
	transition: background-color $trans-default, color $trans-default, box-shadow $trans-default;
	outline-offset: 2px;
	border: none;

	@include vp-767 {
		padding: 15px 30px;
	}

	@include vp-374 {
		font-size: 16px;
		line-height: 18px;
		padding: 15px;
	}

	&:hover,
	&:focus {
		background-color: $color-bright-red;
	}

	&--secondary {
		color: $color-default-white;
		background-color: $color-jaguar;

		&:hover,
		&:focus {
			background-color: $color-tolopea;
		}
	}
}
