.header {
	padding: 16px 0 16px;
	top: 0;
	width: 100%;
	background-color: $color-jaguar;
	z-index: 400;
	position: fixed;

	&__wrapper {
		display: grid;
		align-items: center;
		grid-template-columns: 1fr auto 1fr;
		gap: 15px;
	}

	&__logo {
		display: block;
		width: 84px;
		height: 60px;
		user-select: none;

		@include vp-767 {
			width: 54px;
			height: 39px;
		}
	}

	&__logo-img {
		display: block;
		width: 84px;
		height: 60px;
		overflow: hidden;

		@include vp-767 {
			width: 54px;
			height: 39px;
		}
	}

	&__user-list {
		list-style-type: none;
		margin: 0;
		padding: 0;
		display: flex;
		align-items: center;
		gap: 20px;
		justify-content: flex-end;
	}

	&__user-link {
		display: flex;
		justify-content: center;
		align-items: center;
		border: none;
		background-color: $color-fire-red;
		padding: 0;
		margin: 0;
		height: 56px;
		width: 56px;
		border-radius: 50%;
		cursor: pointer;
		transition: background-color $trans-default;

		&:hover,
		&:focus {
			background-color: $color-bright-red;
		}
	}

	&__user-icon {
		display: block;
		width: 24px;
		height: 24px;
		color: $color-default-white;
	}

	&__phone {
		text-align: right;
		display: block;
		color: $color-default-white;
		font-weight: 600;
		font-size: 30px;
		line-height: 34px;

		@include vp-1279 {
			font-size: 24px;
			line-height: 26px;
		}

		@include vp-1023 {
			grid-column: 2;
			grid-row: 1;
			font-size: 22px;
			line-height: 24px;
		}

		@include vp-374 {
			font-size: 16px;
			line-height: 20px;
		}
	}
}
