.custom-input {
	&.is-invalid .custom-input__field,
	& .custom-input__field[aria-invalid="true"] {
		box-shadow: 0 0 0 2px $color-fire-red inset;
	}

	&__error {
		margin-top: 6px;
		font-size: 14px;
		line-height: 18px;
		color: $color-jaguar;
	}
	
	&__field {
		border: none;
		width: 100%;
		border-radius: 5px;
		height: 64px;
		background-color: $color-snow;
		font-size: 18px;
		line-height: 22px;
		color: $color-jaguar;
		padding: 20px;
		transition: background-color $trans-default, color $trans-default, box-shadow $trans-default;

		&::placeholder {
			color: $color-jaguar-20;
		}

		@include vp-1279 {
			height: 60px;
		}
		@include vp-1023 {
			height: 56px;
			font-size: 16px;
			line-height: 20px;
			padding: 16px;
		}
		@include vp-767 {
			height: 52px;
			font-size: 16px;
			line-height: 20px;
			padding: 14px;
		}
		@include vp-374 {
			height: 48px;
			font-size: 14px;
			line-height: 18px;
			padding: 12px;
		}
	}
}