.tickets {
	padding: 120px 0;
	background-color: $color-snow;

	@include vp-1439 {
		padding: 100px 0;
	}
	@include vp-1279 {
		padding: 80px 0;
	}
	@include vp-1023 {
		padding: 64px 0;
	}
	@include vp-767 {
		padding: 48px 0;
	}
	@include vp-374 {
		padding: 40px 0;
	}

	&__title {
		font-weight: 600;
		font-size: 50px;
		line-height: 54px;
		margin: 0 0 60px;

		@include vp-1439 {
			font-size: 44px;
			line-height: 48px;
			margin-bottom: 52px;
		}
		@include vp-1279 {
			font-size: 38px;
			line-height: 42px;
			margin-bottom: 44px;
		}
		@include vp-1023 {
			font-size: 32px;
			line-height: 36px;
			margin-bottom: 36px;
			text-align: center;
		}
		@include vp-767 {
			font-size: 28px;
			line-height: 32px;
			margin-bottom: 28px;
		}
		@include vp-374 {
			font-size: 24px;
			line-height: 28px;
			margin-bottom: 24px;
		}
	}

	&__wrapper {
		overflow: hidden;
		position: relative;
		margin-bottom: 30px; // отступ для controls

		@include vp-1023 {
			margin-bottom: 24px;
		}
		@include vp-767 {
			margin-bottom: 20px;
		}
	}

	&__list {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	&__controls {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;
		padding: 0; // убираем padding для выравнивания с контейнером

		@include vp-1023 {
			gap: 16px;
		}
		@include vp-767 {
			flex-direction: column;
			align-items: stretch;
			gap: 12px;
		}
	}

	&__navigation {
		display: flex;
		gap: 12px;

		@include vp-1023 {
			gap: 10px;
		}
		@include vp-767 {
			justify-content: center;
		}
	}

	.swiper-wrapper {
		align-items: center;
	}

	// Плавные переходы для слайдов
	.swiper-slide {
		transition: transform 0.6s ease;
		width: auto; // позволяем слайдам иметь естественную ширину
		flex-shrink: 0; // не сжимаем слайды
	}

	.swiper__button {
		position: static; // убираем абсолютное позиционирование
		transform: none; // убираем transform
		width: 48px;
		height: 48px;
		border: none;
		background: $color-jaguar;
		border-radius: 50%;
		color: $color-default-white;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 10;
		transition: all 0.3s ease;

		@include vp-1279 {
			width: 44px;
			height: 44px;
		}
		@include vp-1023 {
			width: 40px;
			height: 40px;
		}
		@include vp-767 {
			width: 36px;
			height: 36px;
		}
		@include vp-374 {
			width: 32px;
			height: 32px;
		}

		&:hover {
			background: darken($color-jaguar, 10%);
			transform: scale(1.1);
		}

		&:active {
			transform: scale(0.95);
		}

		// Состояние неактивной кнопки
		&.swiper-button-disabled {
			opacity: 0.3;
			cursor: not-allowed;
			pointer-events: none;
		}

		// Состояние скрытой кнопки
		&.swiper-button-hidden {
			opacity: 0;
			visibility: hidden;
		}
	}

	.swiper-scrollbar {
		flex: 1; // занимает доступное пространство
		position: relative;
		background: rgba($color-jaguar, 0.2);
		border-radius: 10px;
		height: 6px; // уменьшаем высоту
		cursor: pointer;
		max-width: 400px; // ограничиваем максимальную ширину
		transition: background-color 0.3s ease;
		overflow: hidden; // важно для правильного отображения

		@include vp-1279 {
			max-width: 340px;
		}
		@include vp-1023 {
			max-width: 280px;
		}
		@include vp-767 {
			max-width: 100%;
		}

		&:hover {
			background: rgba($color-jaguar, 0.3);
		}

		.swiper-scrollbar-drag {
			background: $color-jaguar;
			border-radius: 10px;
			height: 100%;
			transition: all 0.3s ease;
			cursor: grab;
			position: relative;
			width: 40px; // минимальная ширина перетаскиваемой части

			&:active {
				cursor: grabbing;
				background: darken($color-jaguar, 10%);
				transform: scaleY(1.2); // немного увеличиваем при перетаскивании
			}
		}

		// Заполненная часть scrollbar (прогресс)
		.swiper-scrollbar-fill {
			background: $color-jaguar;
			height: 100%;
			border-radius: 10px;
			position: absolute;
			top: 0;
			left: 0;
			transition: width 0.3s ease;
		}
	}
}
