.main-nav {
	@include vp-1023 {
		grid-column: 3;
		justify-self: end;
	}

	&__toggle {
		top: 18px;
		right: 27px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 54px;
		height: 54px;
		padding: 0;
		border-radius: 5px;
		border: none;
		cursor: pointer;
		background-color: $color-fire-red;
		transition: background-color $trans-default 0.6s;
		z-index: 200;
		display: none;

		@include vp-1023 {
			display: flex;
		}

		@include vp-767 {
			width: 46px;
			height: 46px;
		}

		@include vp-374 {
			width: 34px;
			height: 34px;
		}
	}

	.header--openned & {
		background-color: transparent;
		transition: background-color $trans-default;

		.main-nav__line {
			background-color: transparent;

			&::after {
				top: 0;
				transform: rotate(45deg);
				transition:
					top $trans-default,
					transform $trans-default 0.3s;
			}

			&::before {
				top: 0;
				transform: rotate(-45deg);
				transition:
					top $trans-default,
					transform $trans-default 0.3s;
			}
		}

		.main-nav__list {
			opacity: 1;
			pointer-events: all;
		}
	}

	.header:not(.header--openned) &:hover,
	.header:not(.header--openned) &:focus-visible {
		.main-nav__line::after,
		.main-nav__line::before {
			transform: scaleX(1.3);
		}
	}

	&__line {
		width: 25px;
		height: 3px;
		background-color: $color-jaguar;
		position: relative;
		transition: background-color $trans-default 0.3s;

		@include vp-374 {
			height: 2px;
			width: 20px;
		}

		&::before,
		&::after {
			background-color: $color-jaguar;
			content: "";
			height: 3px;
			position: absolute;
			top: -8px;
			left: 0;
			width: 25px;
			transition:
				top $trans-default,
				transform $trans-default;

				@include vp-374 {
					height: 2px;
					width: 20px;
					top: -5px;
				}
		}

		&::before {
			transition:
				top $trans-default 0.3s,
				transform $trans-default;
		}

		&::after {
			transition:
				top $trans-default 0.3s,
				transform $trans-default;
			top: 8px;

			@include vp-374 {
				top: 5px;
			}
		}
	}
	&__list {
		display: flex;
		gap: 50px;
		margin: 0;
		padding: 0;
		list-style: none;

		@include vp-1023 {
			opacity: 0;
			pointer-events: none;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			position: fixed;
			width: 50%;
			height: calc(100% - 92px);
			top: 92px;
			right: 0;
			gap: 68px;
			overflow-y: auto;
			background-color: $color-jaguar;
			z-index: 300;
			transition: opacity $trans-default;

			@include vp-767 {
				width: 100%;
				height: calc(100% - 78px);
				top: 78px;
			}

			@include vp-374 {
				top: 71px;
				height: calc(100% - 71px);
			}
		}
	}

	&__item {
		font-size: 20px;
		line-height: 24px;
		font-weight: 500;
	}

	&__link {
		color: $color-default-white;
		transition: color $trans-default;

		&:hover,
		&:focus {
			color: $color-fire-red;
		}

		&.is-active {
			color: $color-fire-red; // подсветка активной ссылки
		}
	}
}
