.faq {
	padding: 120px 0;
	background-color: $color-default-white;

	@include vp-1439 {
		padding: 100px 0;
	}
	@include vp-1279 {
		padding: 80px 0;
	}
	@include vp-1023 {
		padding: 64px 0;
	}
	@include vp-767 {
		padding: 48px 0;
	}
	@include vp-374 {
		padding: 40px 0;
	}

	&__title {
		font-weight: 600;
		font-size: 50px;
		line-height: 54px;
		margin: 0 0 50px;

		@include vp-1439 {
			font-size: 44px;
			line-height: 48px;
			margin-bottom: 44px;
		}
		@include vp-1279 {
			font-size: 38px;
			line-height: 42px;
			margin-bottom: 40px;
		}
		@include vp-1023 {
			font-size: 32px;
			line-height: 36px;
			margin-bottom: 32px;
			text-align: center;
		}
		@include vp-767 {
			font-size: 28px;
			line-height: 32px;
			margin-bottom: 28px;
		}
		@include vp-374 {
			font-size: 24px;
			line-height: 28px;
			margin-bottom: 24px;
		}
	}

	&__accordion {
		display: flex;
		flex-direction: column;
		gap: 40px;
		list-style: none;
		padding: 0;
		margin: 0;

		@include vp-1279 {
			gap: 32px;
		}
		@include vp-1023 {
			gap: 28px;
		}
		@include vp-767 {
			gap: 24px;
		}
		@include vp-374 {
			gap: 20px;
		}
	}

	&__accordion-item {
		border-bottom: 2px solid $color-jaguar;
		position: relative; // на случай псевдоэлементов
	}

	/* чекбокс прячем визуально, но оставляем в потоке для доступности */
	&__accordion-toggle {
		position: absolute;
		opacity: 0;
		pointer-events: none;
	}

	/* Вопрос: текст + кнопка по краям, ровно по вертикали */
	&__accordion-question {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 16px;

		font-size: 24px;
		line-height: 28px;
		font-weight: 600;

		margin: 0;
		padding: 10px 0;
		cursor: pointer;
		user-select: none;

		@include vp-1279 {
			font-size: 22px;
			line-height: 26px;
		}
		@include vp-1023 {
			font-size: 20px;
			line-height: 24px;
			padding: 8px 0;
		}
		@include vp-767 {
			font-size: 18px;
			line-height: 22px;
		}
		@include vp-374 {
			font-size: 16px;
			line-height: 20px;
		}
	}

	/* Кнопка: плюс из двух линий, при открытии поворачиваем весь плюс на 45° => крестик */
	&__accordion-button {
		position: relative;
		inline-size: 24px;
		block-size: 24px;
		flex: 0 0 24px;
		transition: transform 0.25s ease;

		@include vp-1023 {
			inline-size: 20px;
			block-size: 20px;
			flex-basis: 20px;
		}
		@include vp-767 {
			inline-size: 18px;
			block-size: 18px;
			flex-basis: 18px;
		}

		&::after,
		&::before {
			content: "";
			position: absolute;
			left: 50%;
			top: 50%;
			width: 24px;
			height: 2px;
			background-color: $color-jaguar;
			transform: translate(-50%, -50%);
		}
		/* вертикальная палка для плюса */
		&::before {
			transform: translate(-50%, -50%) rotate(90deg);
		}
	}

	/* Поворот плюса в крестик при чекнутом инпуте */
	&__accordion-toggle:checked + .faq__accordion-question .faq__accordion-button {
		transform: rotate(45deg);
	}

	/* Контейнер для контента: плавное открытие/закрытие через grid 0fr -> 1fr */
	&__accordion-inner {
		display: grid;
		grid-template-rows: 0fr; /* закрыто */
		overflow: hidden;
		padding-block: 0;
		transition:
			grid-template-rows 0.28s ease,
			padding 0.28s ease;

		@include vp-767 {
			transition-duration: 0.24s;
		}
	}

	/* Внутренний враппер нужен с overflow, чтобы grid-переход был гладким */
	&__accordion-content {
		overflow: hidden;
	}

	&__accordion-text {
		margin: 0;
		color: $color-jaguar-70;

		@include vp-1023 {
			font-size: 16px;
			line-height: 22px;
		}
		@include vp-374 {
			font-size: 14px;
			line-height: 20px;
		}
	}

	/* Открытие: соседский селектор от чекбокса до inner */
	&__accordion-toggle:checked ~ .faq__accordion-inner {
		grid-template-rows: 1fr; /* открыто */
		padding-block: 20px;
	}

	/* Фокус по табу — удобство навигации */
	&__accordion-question:focus-visible {
		outline: 2px solid $color-jaguar;
		outline-offset: 2px;
		border-radius: 4px;
	}
}
