.loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: $color-snow;
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 9999;
	opacity: 1;
	visibility: visible;
	transition: opacity 0.5s ease, visibility 0.5s ease;
	
	// Блокируем прокрутку во время показа лоадера
	&:not(.hidden) {
		body {
			overflow: hidden;
		}
	}

	&__content {
		text-align: center;
		color: $color-jaguar;
	}

	&__spinner {
		position: relative;
		width: 60px;
		height: 60px;
		margin: 0 auto 30px;
		animation: fadeInUp 0.8s ease-out 0.2s both;
	}

	&__circle {
		width: 100%;
		height: 100%;
		border: 3px solid $color-whisper;
		border-top: 3px solid $color-fire-red;
		border-right: 3px solid $color-fire-red;
		border-radius: 50%;
		animation: rotate 1s linear infinite;
		position: relative;
		transform-origin: center;
		will-change: transform;
		
		// Fallback анимация на случай проблем с keyframes
		@keyframes rotate {
			0% { transform: rotate(0deg); }
			100% { transform: rotate(360deg); }
		}
		
		// Альтернативная анимация через CSS
		&:hover {
			animation-play-state: paused;
		}
		
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 8px;
			height: 8px;
			background: $color-fire-red;
			border-radius: 50%;
			transform: translate(-50%, -50%);
			animation: pulse 1.5s ease-in-out infinite;
			
			// Fallback анимация пульсации
			@keyframes pulse {
				0%, 100% {
					opacity: 1;
					transform: translate(-50%, -50%) scale(1);
				}
				50% {
					opacity: 0.5;
					transform: translate(-50%, -50%) scale(1.2);
				}
			}
		}
	}

	&__text {
		display: flex;
		flex-direction: column;
		gap: 8px;
	}

	&__title {
		font-size: 32px;
		font-weight: 700;
		color: $color-jaguar;
		letter-spacing: 1px;
		animation: fadeInUp 0.8s ease-out 0.3s both;

		@include vp-1023 {
			font-size: 28px;
		}
		@include vp-767 {
			font-size: 24px;
		}
		@include vp-374 {
			font-size: 20px;
		}
	}

	&__subtitle {
		font-size: 14px;
		font-weight: 400;
		color: $color-cloudy-dark;
		opacity: 0.8;
		animation: fadeInUp 0.8s ease-out 0.5s both;

		@include vp-767 {
			font-size: 12px;
		}
	}

	&.hidden {
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
	}
}

// Блокируем анимации до загрузки лоадера
body.loading {
	#main-content {
		// Header и Footer скрыты во время загрузки
		.header,
		.footer {
			opacity: 0;
			transform: translateY(-20px);
			transition: none;
		}
		
		// Hero секция скрыта во время загрузки
		.hero.u-defer {
			opacity: 0;
			transform: translateY(30px);
			transition: none;
		}
		
		.u-defer {
			opacity: 0;
			transform: translateY(20px);
			transition: none;
		}
		
		// Контакты скрыты во время загрузки
		.contacts {
			opacity: 0;
			transform: translateY(20px);
			transition: none;
		}
	}
}

// Активируем анимации после загрузки
body.loaded {
	#main-content {
		// Header и Footer появляются первыми
		.header,
		.footer {
			opacity: 1;
			transform: translateY(0);
			transition: opacity 0.6s ease, transform 0.6s ease;
		}
		
		// Hero секция появляется первой
		.hero.u-defer {
			opacity: 1;
			transform: translateY(0);
			transition: opacity 0.8s ease, transform 0.8s ease;
		}
		
		.u-defer {
			opacity: 1;
			transform: translateY(0);
			transition: opacity 0.6s ease, transform 0.6s ease;
		}
		
		// Убеждаемся, что все элементы с u-defer анимируются
		*[class*="u-defer"] {
			opacity: 1;
			transform: translateY(0);
			transition: opacity 0.6s ease, transform 0.6s ease;
		}
		
		// Анимация с задержкой для разных секций
		.about__wrapper.u-defer {
			transition-delay: 0.1s;
		}
		
		.tickets__wrapper.u-defer {
			transition-delay: 0.2s;
		}
		
		.promo__wrapper.u-defer {
			transition-delay: 0.3s;
		}
		
		.faq__wrapper.u-defer {
			transition-delay: 0.4s;
		}
		
		// Контакты появляются последними
		.contacts {
			opacity: 1;
			transform: translateY(0);
			transition: opacity 0.6s ease, transform 0.6s ease;
			transition-delay: 0.5s;
		}
	}
}

// Анимации для лоадера
@keyframes fadeInUp {
	from {
		opacity: 0;
		transform: translateY(20px);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}