.promo {
	padding: 120px 0;
	background-color: $color-fire-red;

	@include vp-1439 {
		padding: 100px 0;
	}
	@include vp-1279 {
		padding: 80px 0;
	}
	@include vp-1023 {
		padding: 64px 0;
	}
	@include vp-767 {
		padding: 48px 0;
	}
	@include vp-374 {
		padding: 40px 0;
	}

	&__wrapper {
		display: grid;
		grid-template-columns: 1fr 1fr;
		align-items: center;
		justify-content: center;
		gap: 50px;

		@include vp-1279 {
			gap: 40px;
		}
		@include vp-1023 {
			grid-template-columns: 1fr;
			gap: 32px;
			justify-items: center;
		}
		@include vp-767 {
			gap: 28px;
		}
		@include vp-374 {
			gap: 24px;
		}
	}

	&__text {
		color: $color-default-white;
		font-size: 36px;
		line-height: 40px;
		font-weight: 600;

		@include vp-1439 {
			font-size: 32px;
			line-height: 36px;
		}
		@include vp-1279 {
			font-size: 28px;
			line-height: 32px;
		}
		@include vp-1023 {
			font-size: 24px;
			line-height: 28px;
			text-align: center;
		}
		@include vp-767 {
			font-size: 22px;
			line-height: 26px;
		}
		@include vp-374 {
			font-size: 20px;
			line-height: 24px;
		}
	}

	&__form {
		display: flex;
		flex-direction: column;
		gap: 10px;
		width: 100%;
		max-width: 520px;

		@include vp-1279 {
			max-width: 480px;
		}
		@include vp-1023 {
			max-width: 420px;
		}
		@include vp-767 {
			max-width: 100%;
			gap: 8px;
		}
	}

	&__btn {
		width: 100%;
		max-width: 100%;
	}

	// Поля внутри формы
	.custom-input__field {
		width: 100%;
	}
}