.contacts {
	padding: 120px 0 0;
	background-color: $color-snow;

	@include vp-1439 {
		padding: 100px 0 0;
	}
	@include vp-1279 {
		padding: 80px 0 0;
	}
	@include vp-1023 {
		padding: 64px 0 0;
	}
	@include vp-767 {
		padding: 48px 0 0;
	}
	@include vp-374 {
		padding: 40px 0 0;
	}

	&__title {
		font-weight: 600;
		font-size: 50px;
		line-height: 54px;
		margin: 0 0 50px;

		@include vp-1439 {
			font-size: 44px;
			line-height: 48px;
			margin-bottom: 44px;
		}
		@include vp-1279 {
			font-size: 38px;
			line-height: 42px;
			margin-bottom: 40px;
		}
		@include vp-1023 {
			font-size: 32px;
			line-height: 36px;
			margin-bottom: 32px;
			text-align: center;
		}
		@include vp-767 {
			font-size: 28px;
			line-height: 32px;
			margin-bottom: 28px;
		}
		@include vp-374 {
			font-size: 24px;
			line-height: 28px;
			margin-bottom: 24px;
		}
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		gap: 50px;
		contain: none;

		@include vp-1279 {
			gap: 40px;
		}
		@include vp-1023 {
			gap: 32px;
		}
		@include vp-767 {
			gap: 28px;
		}
		@include vp-374 {
			gap: 24px;
		}
	}
	&__map {
		isolation: isolate;
		position: relative;
		z-index: 0;
		width: 100%;
		height: 400px;
		border-radius: 12px;
		overflow: hidden;
		box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
		background-color: #f5f5f5;
		transition: opacity 0.3s ease;

		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 40px;
			height: 40px;
			border: 3px solid #e0e0e0;
			border-top: 3px solid #007bff;
			border-radius: 50%;
			animation: spin 1s linear infinite;
			z-index: 1;
		}

		&.loaded::before {
			display: none;
		}

		iframe {
			width: 100%;
			height: 100%;
			border: none;
			display: block;
			opacity: 0;
			transition: opacity 0.3s ease;

			&.loaded {
				opacity: 1;
			}
		}

		@include vp-1023 {
			height: 350px;
			border-radius: 8px;
		}
		@include vp-767 {
			height: 300px;
			border-radius: 6px;
		}
		@include vp-374 {
			height: 250px;
		}
	}

	&__inner {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		gap: 20px;

		@include vp-1023 {
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
			gap: 16px;
		}
	}

	&__text {
		font-size: 18px;
		line-height: 24px;
		font-weight: 400;
		padding: 0;
		margin: 0;
		max-width: 80%;

		@include vp-1023 {
			font-size: 16px;
			line-height: 22px;
		}
		@include vp-374 {
			font-size: 14px;
			line-height: 20px;
		}
	}
}
