.about {
	padding: 120px 0;
	background-color: $color-default-white;

	@include vp-1439 {
		padding: 100px 0;
	}
	@include vp-1279 {
		padding: 80px 0;
	}
	@include vp-1023 {
		padding: 64px 0;
	}
	@include vp-767 {
		padding: 48px 0;
	}
	@include vp-374 {
		padding: 40px 0;
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		gap: 50px;

		@include vp-1439 {
			gap: 40px;
		}
		@include vp-1279 {
			gap: 36px;
		}
		@include vp-1023 {
			gap: 32px;
			align-items: center;
			text-align: center;
		}
		@include vp-767 {
			gap: 28px;
		}
		@include vp-374 {
			gap: 24px;
		}
	}

	&__text {
		font-size: 30px;
		line-height: 34px;
		font-weight: 400;
		padding: 0;
		margin: 0;

		@include vp-1919 {
			font-size: 28px;
			line-height: 32px;
		}
		@include vp-1439 {
			font-size: 26px;
			line-height: 30px;
		}
		@include vp-1279 {
			font-size: 22px;
			line-height: 28px;
		}
		@include vp-1023 {
			font-size: 20px;
			line-height: 26px;
		}
		@include vp-767 {
			font-size: 18px;
			line-height: 24px;
		}
		@include vp-374 {
			font-size: 16px;
			line-height: 20px;
		}

		&--strong {
			font-weight: 600;
		}
	}

	// Кнопка в блоке About
	.btn {
		align-self: flex-start;

		@include vp-1023 {
			align-self: center;
		}
	}
}